import React, { FunctionComponent } from "react";
import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";
import logo from "assets/img/logos/brex/logo-white.svg";
import CompanyTypes from "utils/companyTypes";
import { path } from "ramda";
import {
  Container,
  Logo,
  LogoWrapper,
  Line,
  FooterHeading,
  LegalText,
  Content,
  BrexLogo,
  BrexLogoWrapper,
  FullBrexLogo,
} from "./elements";

interface Image {
  fields: {
    file: {
      url: string;
    };
  };
}

interface Props {
  partnerLogo: Image;
  companyType?: CompanyTypes;
  refCode: string;
  finalHeading: string;
  gutterBackground?: string;
  analyticsPath: string;
  useCombinedLogo?: boolean | undefined;
}

const PartnerFooter: FunctionComponent<Props> = ({
  partnerLogo,
  refCode,
  companyType,
  finalHeading,
  analyticsPath,
  useCombinedLogo,
}) => (
  <Container>
    <Content>
      {useCombinedLogo ? (
        <BrexLogoWrapper>
          <FullBrexLogo
            src={path(["fields", "file", "url"], partnerLogo)}
            alt="Brex Partner Logo"
          />
        </BrexLogoWrapper>
      ) : (
        <LogoWrapper>
          <BrexLogo src={logo} alt="Brex" />
          <Line />
          <Logo
            src={path(["fields", "file", "url"], partnerLogo)}
            alt="PartnerLogo"
          />
        </LogoWrapper>
      )}
      <FooterHeading>{finalHeading}</FooterHeading>
      <ApplyButton
        analyticsPath={analyticsPath}
        refCode={refCode}
        companyType={companyType}
      >
        sign up
      </ApplyButton>
      <LegalText>
         {new Date().getFullYear()} Brex Inc. Brex and the Brex logo are
        registered trademarks.
        <br />
        <br />
        The Brex Mastercard Corporate Credit Card is issued by Emigrant Bank,
        Member FDIC. Terms and conditions apply. See the Brex Platform Agreement
        for details.
      </LegalText>
    </Content>
  </Container>
);

export default PartnerFooter;
