import React, { FunctionComponent } from "react";
import Layout from "components/layout";
import {
  Hero,
  PartnerHeader,
  TrustedBy,
  SimpleValuePropsContainer,
  SingleLineWithAction,
  RewardInfo,
  PartnerQuotes,
  PartnerFooter,
  PerkBrands,
} from "components/reusable/molecules";
import { AlternatingSequence } from "components/reusable/organisms";
import { messages } from "../messages.js";
import CompanyTypes from "utils/companyTypes";
import heroImage from "assets/img/partner-pages/slantedCard.png";
import colors from "styles/colors";

interface Image {
  fields: {
    file: {
      url: string;
    };
  };
}
interface Props {
  //TODO: runtime typing for contentful data ref: Justin
  location: Location;
  pageContext: {
    contentfulData: {
      partnerType: string;
      partnerName: string;
      partnerLogo: Image;
      refCode: string;
      headlineText: string;
      headlineEmphasis: string;
      headlineSubtitle: string;
      useCombinedLogo?: boolean | undefined;
      partnerQuote: {
        fields: {
          quoteLogo: Image;
          quote: string;
          quoteAuthor: string;
          authorTitle: string;
        };
      };
      finalHeading: string;
      urlPath: string;
    };
    partnerType: CompanyTypes;
  };
}

const PartnerTemplate: FunctionComponent<Props> = ({
  pageContext: { contentfulData, partnerType },
  location,
}) => {
  return (
    <Layout
      location={location}
      title={`${contentfulData.partnerName} & Brex`}
      description="Credit card with no personal guarantee, instant approvals, higher limits, and modern payment terms. Powered by the Mastercard network with best-in-class fraud protection."
      hideDefaultHeader
      hideDefaultFooter
      indexPage={false}
      whiteContentBackground
      whiteGutterBackground
    >
      <PartnerHeader
        gutterBackground={colors.black}
        partnerLogo={contentfulData.partnerLogo}
        useCombinedLogo={contentfulData.useCombinedLogo}
      />
      <Hero
        analyticsPath={`${contentfulData.urlPath}.Hero.cta`}
        companyType={partnerType}
        refCode={contentfulData.refCode}
        gutterBackground={colors.black}
        heroImage={heroImage}
        title={contentfulData.headlineText}
        subTitle={contentfulData.headlineSubtitle}
        emphasis={contentfulData.headlineEmphasis}
      />
      <TrustedBy
        gutterBackground={colors.black}
        title="Trusted By"
        customers={messages[partnerType].customers}
      />
      <SimpleValuePropsContainer
        padding={60}
        valueProps={messages[partnerType].valueProps}
      />
      <AlternatingSequence
        messages={messages[partnerType].alternatingSequenceMessages}
      />
      <SingleLineWithAction
        content={messages[partnerType].singleCTAText}
        refCode={contentfulData.refCode}
        companyType={partnerType}
        analyticsPath={`${contentfulData.urlPath}.SingleLineWithAction.cta`}
      />
      {messages[partnerType].perks && (
        <PerkBrands
          gutterBackground={colors.black}
          title={messages[partnerType].perkTitle}
          perkBrands={messages[partnerType].perks}
        />
      )}
      {messages[partnerType].rewards && (
        <RewardInfo
          gutterBackground={colors.black}
          title={messages[partnerType].rewardTitle}
          rewards={messages[partnerType].rewards}
        />
      )}
      <PartnerQuotes partnerQuote={contentfulData.partnerQuote} />
      <PartnerFooter
        gutterBackground={colors.black}
        partnerLogo={contentfulData.partnerLogo}
        companyType={partnerType}
        refCode={contentfulData.refCode}
        finalHeading={contentfulData.finalHeading}
        analyticsPath={`${contentfulData.urlPath}.PartnerFooter.cta`}
        useCombinedLogo={contentfulData.useCombinedLogo}
      />
    </Layout>
  );
};

export default PartnerTemplate;
