import React, { FunctionComponent } from "react";
import BrandLogo from "components/reusable/atoms/BrandLogo";

import { TrustedByWrapper, Title, LogoWrapper, Logo } from "./elements";

// Base component to created the TrustedBy section
// Assumes logos are passed on
interface Customers {
  logo: any;
  key: string;
  height?: number;
  width?: number;
  gray?: boolean;
}

interface Props {
  customers: Array<Customers>;
  title: string;
  gutterBackground?: string;
  color?: string;
}

const TrustedBy: FunctionComponent<Props> = ({ customers, title, color }) => (
  <TrustedByWrapper color={color}>
    <Title>{title}</Title>
    <LogoWrapper>
      {customers.map(({ logo, key, height, width, gray }) => (
        <Logo key={key}>
          <BrandLogo logo={logo} height={height} width={width} gray={gray} />
        </Logo>
      ))}
    </LogoWrapper>
  </TrustedByWrapper>
);

export default TrustedBy;
