import React, { FunctionComponent } from "react";
import { path } from "ramda";
import {
  Container,
  LogoWrapper,
  Logo,
  QuoteWrapper,
  Quote,
  Author,
} from "./elements";

interface Props {
  partnerQuote: PartnerQuote;
}

interface Image {
  fields: {
    file: {
      url: string;
    };
  };
}

interface PartnerQuote {
  fields: {
    authorTitle: string;
    quoteLogo: Image;
    quote: string;
    quoteAuthor: string;
  };
}

const PartnerQuotes: FunctionComponent<Props> = ({ partnerQuote }) => (
  <Container>
    <LogoWrapper>
      <Logo
        src={path(["fields", "file", "url"], partnerQuote.fields.quoteLogo)}
      />
    </LogoWrapper>
    <QuoteWrapper>
      <Quote>{partnerQuote.fields.quote}</Quote>
      <Author>
        {partnerQuote.fields.quoteAuthor}
        ,&nbsp;
        <span>{partnerQuote.fields.authorTitle}</span>
      </Author>
    </QuoteWrapper>
  </Container>
);

export default PartnerQuotes;
