import React, { FunctionComponent } from "react";
import { Row, Column } from "components/Grid";

import {
  Title,
  Subtitle,
  TitleWrapper,
  Wrapper,
  DetailsSection,
  Section,
  Image,
  Number,
  SectionTitle,
  SectionDescription,
} from "./elements";

interface Prop {
  messages: Message;
  steps?: boolean;
}

interface Message {
  title: string;
  subtitle?: string;
  sections: Array<Sections>;
}

interface Sections {
  title: string;
  description: string;
  img: string;
}

const AlternatingSequence: FunctionComponent<Prop> = ({ messages, steps }) => (
  <Wrapper>
    <TitleWrapper>
      <Title>{messages.title}</Title>
      <Subtitle>{messages.subtitle}</Subtitle>
    </TitleWrapper>
    <DetailsSection>
      {messages.sections.map((section, index) => (
        <Section key={section.title}>
          <Row
            reversedFlexDirection={index % 2 === 0}
            verticalAlignment="center"
            horizontalAlignment="space-between"
          >
            <Column span={5} spanMedium={12}>
              <Image src={section.img} alt={section.title} />
              {steps && <Number>{`0${index + 1}`}</Number>}
            </Column>
            <Column span={2} spanMedium={0} />
            <Column span={5} spanMedium={12}>
              <SectionTitle>{section.title}</SectionTitle>
              <SectionDescription>{section.description}</SectionDescription>
            </Column>
          </Row>
        </Section>
      ))}
    </DetailsSection>
  </Wrapper>
);

export default AlternatingSequence;
