import React, { FunctionComponent } from "react";
import { Row, Column } from "components/Grid";
import SimpleValueProp from "./SimpleValueProp";
import { Container, ContentWrapper } from "./elements";

interface valueProps {
  icon: string;
  title: string;
  caption: string;
  list: Array<string>;
  light?: boolean;
}

interface Props {
  valueProps: Array<valueProps>;
  padding: number;
}

const SimpleValuePropsContainer: FunctionComponent<Props> = ({
  valueProps,
  padding,
}) => (
  <Container>
    <Row padding={padding} gutterWidth={26}>
      <Column span={12}>
        <ContentWrapper>
          {valueProps.map((prop) => (
            <SimpleValueProp
              icon={prop.icon}
              title={prop.title}
              caption={prop.caption}
              key={prop.title}
              list={prop.list}
              light={prop.light}
            />
          ))}
        </ContentWrapper>
      </Column>
    </Row>
  </Container>
);

export default SimpleValuePropsContainer;
