import React, { FunctionComponent } from "react";
import {
  Wrapper,
  Icon,
  Title,
  Content,
  Link,
  BulletPointWrapper,
  Bullet,
} from "./elements";

interface Link {
  text: string;
  href: string;
}

interface Prop {
  icon: string;
  title: string;
  caption?: string;
  list?: Array<string>;
  light?: boolean;
  link?: Link;
}

const SimpleValueProp: FunctionComponent<Prop> = ({
  icon,
  title,
  caption,
  light = false,
  link,
  list,
}) => (
  <Wrapper>
    <Icon src={icon} iconWidth={56} iconHeight={56} />
    <Title light={light}>{title}</Title>
    {caption && <Content light={light}>{caption}</Content>}
    {list &&
      list.map((valueProp) => (
        <BulletPointWrapper>
          <Bullet />
          <Content light={light}>{valueProp}</Content>
        </BulletPointWrapper>
      ))}
    {link && <Link href={link.href}>{link.text}</Link>}
  </Wrapper>
);

export default SimpleValueProp;
