import React, { FunctionComponent } from "react";
import Layout from "components/layout";
import {
  Hero,
  PartnerHeader,
  TrustedBy,
  SimpleValuePropsContainer,
  SingleLineWithAction,
  RewardInfo,
  PartnerQuotes,
  PartnerFooter,
  PerkBrands,
  Tabs,
} from "components/reusable/molecules";
import { AlternatingSequence } from "components/reusable/organisms";
import { compose, lifecycle, StateHandler, withStateHandlers } from "recompose";
import { messages, tabs } from "../messages.js";
import heroImage from "assets/img/solution/brex-card-fan-black.png";
import colors from "styles/colors";

interface Image {
  fields: {
    file: {
      url: string;
    };
  };
}
interface Props {
  location: Location;
  pageContext: {
    contentfulData: {
      partnerType: string;
      partnerName: string;
      partnerLogo: Image;
      refCode: string;
      headlineText: string;
      headlineEmphasis: string;
      headlineSubtitle: string;
      useCombinedLogo?: boolean | undefined;
      partnerQuote: {
        fields: {
          quoteLogo: Image;
          quote: string;
          quoteAuthor: string;
          authorTitle: string;
        };
      };
      finalHeading: string;
      urlPath: string;
    };
  };
  selected: number;
  changeTab: Function;
}
interface CycleProps {
  changeTab: StateHandler<{
    selected: number;
  }>;
}

const GeneralPartnerTemplate: FunctionComponent<Props> = ({
  pageContext: { contentfulData },
  location,
  selected,
  changeTab,
}) => {
  let currentTab = "STARTUP";
  switch (selected) {
    case 0:
      currentTab = "STARTUP";
      break;
    case 1:
      currentTab = "ECOMMERCE";
      break;
    case 2:
      currentTab = "LIFESCIENCE";
      break;
    default:
      // do nothing
      break;
  }

  return (
    <Layout
      location={location}
      title={`${contentfulData.partnerName} & Brex`}
      description="Credit card with no personal guarantee, instant approvals, higher limits, and modern payment terms. Powered by the Mastercard network with best-in-class fraud protection."
      hideDefaultHeader
      hideDefaultFooter
      indexPage={false}
      whiteContentBackground
      whiteGutterBackground
    >
      <PartnerHeader
        gutterBackground={colors.black}
        partnerLogo={contentfulData.partnerLogo}
        useCombinedLogo={contentfulData.useCombinedLogo}
      />
      <Hero
        analyticsPath={`${contentfulData.urlPath}.Hero.cta`}
        refCode={contentfulData.refCode}
        gutterBackground={colors.black}
        heroImage={heroImage}
        title={contentfulData.headlineText}
        subTitle={contentfulData.headlineSubtitle}
        emphasis={contentfulData.headlineEmphasis}
      />
      <Tabs
        gutterBackground={colors.black}
        tabs={tabs}
        changeTab={changeTab}
        selected={selected}
        color="black"
      />
      <SimpleValuePropsContainer
        padding={60}
        valueProps={messages[currentTab].valueProps}
      />
      <TrustedBy
        title="Trusted By"
        customers={messages[currentTab].whiteCustomers}
        color="white"
      />
      <AlternatingSequence
        messages={messages[currentTab].alternatingSequenceMessages}
      />
      <SingleLineWithAction
        content={messages[currentTab].singleCTAText}
        refCode={contentfulData.refCode}
        analyticsPath={`${contentfulData.urlPath}.SingleLineWithAction.cta`}
      />
      {messages[currentTab].perks && (
        <PerkBrands
          gutterBackground={colors.black}
          title={messages[currentTab].perkTitle}
          perkBrands={messages[currentTab].perks}
        />
      )}
      {messages[currentTab].rewards && (
        <RewardInfo
          gutterBackground={colors.black}
          title={messages[currentTab].rewardTitle}
          rewards={messages[currentTab].rewards}
        />
      )}
      <PartnerQuotes partnerQuote={contentfulData.partnerQuote} />
      <PartnerFooter
        gutterBackground={colors.black}
        partnerLogo={contentfulData.partnerLogo}
        refCode={contentfulData.refCode}
        finalHeading={contentfulData.finalHeading}
        analyticsPath={`${contentfulData.urlPath}.SingleLineWithAction.cta`}
        useCombinedLogo={contentfulData.useCombinedLogo}
      />
    </Layout>
  );
};

export default compose<Props, { selected: number }>(
  withStateHandlers(
    () => ({
      selected: 0,
    }),
    {
      changeTab: () => (item: number) => {
        let hash = "startup";
        if (item === 1) {
          hash = "ecommerce";
        }
        if (item === 2) {
          hash = "lifescience";
        }
        if (typeof window !== "undefined") {
          window.location.hash = hash;
        }
        return {
          selected: item,
        };
      },
    },
  ),
  lifecycle<CycleProps, {}, {}>({
    componentDidMount() {
      let selectedFromHash = 0;
      if (typeof window !== "undefined") {
        if (window.location.hash === "#ecommerce") {
          selectedFromHash = 1;
        }
        if (window.location.hash === "#lifescience") {
          selectedFromHash = 2;
        }
        if (window.location.hash.length === 0) {
          if (typeof window !== "undefined") {
            window.location.hash = "startup";
          }
        }
      }

      this.props.changeTab(selectedFromHash);
    },
  }),
)(GeneralPartnerTemplate);
