import React from "react";
import qs from "qs";
import { path, isEmpty, isNil } from "ramda";

import {
  lifecycle,
  compose,
  withStateHandlers,
  branch,
  renderComponent,
} from "recompose";

import PartnerTemplate from "components/contentful-templates/partner-pages/partner-template";
import GeneralPartnerTemplate from "components/contentful-templates/partner-pages/general-template";

import { getPreviewClient } from "../utils";

import { MessageWrapper } from "./elements";

const ContentfulPreview = ({ pageData, location }) => {
  const partnerType = pageData.fields.partnerType.toUpperCase();
  return partnerType === "GENERAL" ? (
    <GeneralPartnerTemplate
      pageContext={{ contentfulData: pageData.fields, partnerType }}
      location={location}
    />
  ) : (
    <PartnerTemplate
      pageContext={{ contentfulData: pageData.fields, partnerType }}
      location={location}
    />
  );
};

const _lifecycle = {
  async componentDidMount() {
    const landingPageSpaceClient = getPreviewClient(
      process.env.CONTENTFUL_LANDING_PAGE_SPACE_ACCESS_TOKEN_PREVIEW,
      process.env.CONTENTFUL_LANDING_PAGE_SPACE_ID,
    );

    try {
      const urlSearch = qs.parse(window.location.search, {
        ignoreQueryPrefix: true,
      });
      const url = path(["url"], urlSearch);

      if (isNil(url)) {
        throw Error(
          "Please specify a URL to test via a URL parameter (i.e. www.brex.com/contentful-preview?url=lifetest)",
        );
      }
      const entries = await landingPageSpaceClient.getEntries({
        content_type: "partnerPages",
        "fields.urlPath": url,
      });

      if (isEmpty(entries.items)) {
        throw Error(
          "The URL path that was provide was not matched to an learn article. Please try again with a different path.",
        );
      }
      this.props.setPageData(entries.items[0]);
    } catch (error) {
      this.props.setError(error.message);
    }
  },
};

export default compose(
  withStateHandlers(
    {
      pageData: null,
      error: null,
    },
    {
      setPageData: () => (data) => ({ pageData: data }),
      setError: () => (errorMessage) => ({ error: errorMessage }),
    },
  ),
  lifecycle(_lifecycle),
  branch(
    ({ error }) => !!error,
    renderComponent(({ error }) => <MessageWrapper>{error}</MessageWrapper>),
  ),
  branch(
    ({ pageData }) => !pageData,
    renderComponent(() => (
      <MessageWrapper>Fetching contentful data...</MessageWrapper>
    )),
  ),
)(ContentfulPreview);
