import styled from "@emotion/styled";
import { breakPoints } from "components/Grid";

export const BrandLogoWrapper = styled("div")(({ centered }) => ({
  alignItems: "center",
  display: "flex",
  justifyContent: centered ? "center" : "flex-start",
  textAlign: "center",
  minHeight: 60,
  paddingBottom: 50,
  [breakPoints.small]: {
    justifyContent: "center",
  },
}));

export const BrandLogoImage = styled("img")(({ width, height, gray }) => ({
  height,
  width,
  opacity: gray ? 0.8 : 1,
  [breakPoints.mediumLaptop]: {
    height: height - height * 0.2,
    width: width - width * 0.2,
  },
  [breakPoints.small]: {
    height: height - height * 0.2,
    width: width - width * 0.2,
  },
}));
