import React, { FunctionComponent } from "react";
import ApplyButton from "components/reusable/atoms/buttons/ApplyButton";
import { Container, Title } from "./elements";
import { Row } from "components/Grid";
import CompanyTypes from "utils/companyTypes";

interface Props {
  content: string;
  refCode: string;
  companyType?: CompanyTypes;
  analyticsPath: string;
}

const SingleLineWithAction: FunctionComponent<Props> = ({
  content,
  refCode,
  analyticsPath,
  companyType,
}) => (
  <Row padding={60}>
    <Container>
      <Title>{content}</Title>
      <ApplyButton
        refCode={refCode}
        analyticsPath={analyticsPath}
        companyType={companyType}
      >
        Sign up
      </ApplyButton>
    </Container>
  </Row>
);

export default SingleLineWithAction;
