import React from "react";
import { path } from "ramda";
import InternalLink from "components/reusable/atoms/links/InternalLink";
import logo from "assets/img/logos/brex/logo-white.svg";
import { Navbar, Logo, Line, BrexLogoWrapper, FullBrexLogo } from "./elements";

interface Image {
  fields: {
    file: {
      url: string;
    };
  };
}

interface PartnerHeaderProps {
  partnerLogo: Image;
  gutterBackground?: string;
  useCombinedLogo?: boolean | undefined;
}

const PartnerHeader: React.FunctionComponent<PartnerHeaderProps> = ({
  partnerLogo,
  useCombinedLogo,
}) => (
  <>
    {useCombinedLogo ? (
      <BrexLogoWrapper>
        <FullBrexLogo
          src={path(["fields", "file", "url"], partnerLogo)}
          alt="Brex Partner Logo"
        />
      </BrexLogoWrapper>
    ) : (
      <Navbar>
        <div>
          <InternalLink to="/">
            <Logo src={logo} alt="Brex Logo" />
          </InternalLink>
        </div>
        <Line />
        <div>
          <Logo
            src={path(["fields", "file", "url"], partnerLogo)}
            alt="Brex Partner Logo"
          />
        </div>
      </Navbar>
    )}
  </>
);

export default PartnerHeader;
