import React from "react";
import {
  Container,
  Title,
  Subtitle,
  RewardsContainer,
  MultiplierContainer,
  MultiplierNumber,
  SmallCaption,
  Body,
} from "./elements";

interface Props {
  title: string;
  subTitle?: string;
  rewards: Array<Rewards>;
  gutterBackground?: string;
}

interface Rewards {
  number: number;
  title: string;
  body: string;
}

const RewardInfo: React.FunctionComponent<Props> = ({
  rewards,
  title,
  subTitle,
}) => (
  <Container>
    <Title>{title}</Title>
    <Subtitle>{subTitle}</Subtitle>
    <RewardsContainer>
      {rewards.map((reward) => (
        <MultiplierContainer key={reward.title}>
          <MultiplierNumber>{`${reward.number}x`}</MultiplierNumber>
          <SmallCaption>{reward.title}</SmallCaption>
          <Body>{reward.body}</Body>
        </MultiplierContainer>
      ))}
    </RewardsContainer>
  </Container>
);

export default RewardInfo;
