import React, { FunctionComponent } from "react";
import BrandLogo from "components/reusable/atoms/BrandLogo";
import { Container, Title, LogoWrapper, Logo } from "./elements";

interface Brands {
  logo: string;
  key: string;
  height?: number;
  width?: number;
  gray?: boolean;
}

interface Props {
  title: string;
  perkBrands: Array<Brands>;
  gutterBackground?: string;
}

const PerkBrands: FunctionComponent<Props> = ({ title, perkBrands }) => (
  <Container>
    <Title>{title}</Title>
    <LogoWrapper>
      {perkBrands.map(({ logo, key, height, width, gray }) => (
        <Logo key={key}>
          <BrandLogo
            logo={logo}
            height={height}
            width={width}
            gray={gray}
            centered={false}
          />
        </Logo>
      ))}
    </LogoWrapper>
  </Container>
);

export default PerkBrands;
