import React, { FunctionComponent } from "react";
import { TabWrapper, TabItem } from "./elements";

interface Props {
  selected: number;
  changeTab: Function;
  tabs: Array<string>;
  color: string;
  gutterBackground?: string;
}

const Tabs: FunctionComponent<Props> = ({
  selected,
  changeTab,
  tabs,
  color,
}) => (
  <TabWrapper color={color}>
    {tabs.map((content, index) => (
      <TabItem
        key={index}
        selected={selected === index}
        onClick={() => changeTab(index)}
      >
        {content}
      </TabItem>
    ))}
  </TabWrapper>
);

export default Tabs;
