import React from "react";
import { BrandLogoWrapper, BrandLogoImage } from "./elements";

const BrandLogo = ({
  logo,
  height = 40,
  width = 100,
  gray = false,
  centered = true,
}) => (
  <BrandLogoWrapper centered={centered}>
    <BrandLogoImage src={logo} height={height} width={width} gray={gray} />
  </BrandLogoWrapper>
);

export default BrandLogo;
